* {
    margin: 0;
    padding: 0;
    /* color: #F15D22; */
    box-sizing: border-box;
}

.img_ {
    width: 150px;
    height: 150px;
    border-radius: 10px;
    border: 3px solid #cecece;
    overflow: hidden;
    /* margin: 0 auto; */
}

.img_res_ {
    width: 100%;
    z-index: 999;
}

hr {
    margin-top: 1rem;
    margin-bottom: 1rem;
    border: 0;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.list_type_none {
    list-style-type: none;
}

.list_item_ {
    text-align: center;
    float: left;
    margin: 0 5px;
}

.list_item__ {
    margin-left: 20px;
    margin-bottom: 2px;
    box-shadow: 0px 3px 6px -4px rgba(0,0,0,0.3);
    padding: 4px;
    font-size: 14px;
}

.spacing_ {
    letter-spacing: 0.04rem;
}

.block_ {
    width: 30px;
    height: 30px;
    line-height: 30px;
    font-size: 16px;
    box-shadow: 0px 2px 3px 4px rgba(0, 0, 0, 0.3);
    background: #ffffff;
    border-radius: 4px;
    text-align: center;
    color: #000000;
    margin: 0 auto;
    transition: 0.3s;
}

.block_:hover {
    background-color: royalblue;
}

.section_ {
    display: grid;
    margin: 20px 0;
    /* border-bottom: 1px solid rgba(0,0,0,0.1); */
}

.header_ {
    color: #ffffff;
    background: linear-gradient(45deg, #000000, transparent);
    text-transform: uppercase;
    font-size: 22px;
    padding: 5px 10px;
}

.card_header_ {
    padding: 0;
}

.sml_ {
    font-size: 12px;
    font-family: 'Courier New', Courier, monospace;
}